@charset "UTF-8";
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap');

/*Generic CSS*/

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --font-main:  'Poppins', sans-serif;

    --fs-xl: 3rem;
    --fs-600: 2rem;
    --fs-500: 1.5rem;
    --fs-400: 1rem;
    --fs-200: .6rem;

    --fw-regular: 400;
    --fw-bold: 700;
}

body,
input,
textarea,
button,
h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: var(--font-main);
    font-size: var(--fs-400);
    font-weight: var(--fw-regular);
    color: #161616;
}

button, input {
    appearance: none;
    border: none;
    background: none;
    cursor: pointer;
    outline: none;
}

ul,
ol {
    list-style: none;
}

a {
    text-decoration: none;
    color: inherit;
}

img {
    max-width: 100%;
}


body {
    color: #001635;
    background-color: #edfeff;
}

main {
    max-width: 720px;
    width: 90%;
}

.table {
    position: relative;
    display: grid;
    width: 100%;
    grid-template-columns: repeat(4, 1fr);
    gap: 5px;
}

.table-win {
    position: absolute;
    background-color: rgb(140, 0, 0);
    z-index: 999;
    pointer-events: none;
}

.square {
    position: relative;
    aspect-ratio: 1/1;
    width: 100%;
    border-radius: 5px;
    background-color: #a2c9ff;
}

.square:has(.piece:not(.placed)) {
    cursor: pointer;
}

.side-table {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(2, 1fr);
    gap: 5px;
}

.side-square {
    aspect-ratio: 1/1;
    position: relative;
    width: 100%;
    border-radius: 5px;
    background-color: #4e709f; 
}

footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
    max-width: 500px;
    margin: 0 auto;
    gap: 1rem;
}

.stage {
    text-transform: uppercase;
    font-size: var(--fs-500);
    font-weight: var(--fw-bold);
}

.progress {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    gap: .25rem;
}

.bar {
    position: relative;
    display: inline-block;
    border-radius: 20px;
    width: 100%;
    height: 20px;
    background: #a2c9ff;
    overflow: hidden;
}

.bar-fill {
    height: 100%;
    background-color: #4e709f;
}

.side-table:not(.blocked) .side-square:has(.piece):hover { cursor: pointer }
.side-table:not(.blocked) .side-square:hover > .piece { opacity: .5 }

/* Classifications */

.piece {
    aspect-ratio: 1/1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.piece-grab {
    aspect-ratio: 1/1;
    position: absolute;
    width: 50px;
}

/* .flat {} */
.pierced::after { 
    aspect-ratio: 1/1;
    position: absolute;
    content: '';
    width: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 5px;
}
.side-square .pierced::after { background-color: #4e709f; }
.square .pierced::after { background-color: #a2c9ff; }
.pierced.circle::after { border-radius: 50%;}

.dark { background-color: #161616; }
.light { background-color: #edfeff; }

.circle { border-radius: 50%; }
/* .squared {} */

.small { width: 50%; }
.large { width: 80%; }

.canva {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;

    z-index: 999;
}

@media (min-width: 501px) {
    #root {
        height: 100%;
        display: flex;
        flex-direction: column;
        gap: 2rem;
        align-items: center;
        justify-content: center;
    }

    html {
        overflow: hidden;
    }

    main {
        display: grid;
        grid-template-columns: 1fr 50% 1fr;
        gap: 1.5rem;
        align-items: center;
        justify-content: center;
    }
}

@media (max-width: 500px) {
    main {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: clamp(50%, 40vh, 80%);
        gap: 1rem;
        margin: 2rem auto;
        padding: 2rem 1rem;
    }

    .side-table {
        grid-template-columns: repeat(4, 1fr);
        width: 80%;
    }
}

@keyframes leftToRight {
    0% {
        transform: translateX(-100%);
    }
    20% {
        transform: translateX(0);
    }
    80% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(100%);
    }
}

@keyframes fade {
    0% {
        opacity: 0;
    }
    80% {
        opacity: 0;
    }
    100% {
        opacity: 100%;
    }
}
  
.popup {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    z-index: 9999;
}

.turn {
    color: white;
    font-size: var(--fs-xl);
    font-weight: var(--fw-bold);
}

.leftToRight {
    transform: translateX(100%);
    animation-name: leftToRight;
    animation-duration: 2s;
    animation-timing-function: ease;
    pointer-events: none;
}

.fade {
    animation-name: fade;
    animation-duration: 1.25s;
    animation-timing-function: ease;
}

.bttn {
    background-color: #4e709f;
    color: white;
    /* font-size: var(--fs-500); */
    font-weight: var(--fw-bold);
    padding: .25em .75em;
    border-radius: 5px;
    transition: opacity .2s ease;
}

.bttn:hover {
    opacity: .75;
}

.box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    padding: 1.5em;
    border-radius: 10px;
    background-color: white;
}

.box header {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.box h2 {
    font-size: var(--fs-600);
    font-weight: var(--fw-bold);
}

.box-rects {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.box-rect {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 5rem;
    padding: .75em;

    border-radius: 5px;
    background-color: #dfdfdf;
}

.class {
    font-weight: var(--fw-bold);
}